import React, {FC} from 'react';
import s from './Dialogs.module.css'
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {InjectedFormProps, reduxForm} from "redux-form";
import {createField, Textarea} from "../commons/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/helpers/validators";
import {InitialStateType} from "../../redux/dialogs-reducer";


type OwnPropsType = {
    dialogsPage: InitialStateType
    sendMessage: (messageText: string) => void
}
export type NewMessageFormType = {
    newMessageBody: string
}


const Dialogs: FC<OwnPropsType> = (props) => {
    const state = props.dialogsPage

    const dialogsElements = state.dialogs.map(d => <DialogItem key={d.id} name={d.name} id={d.id}/>)
    const messagesElements = state.messages.map(m => <Message key={m.id} message={m.message}/>)

    const addNewMessage = (values: NewMessageFormType) => {
        if(values.newMessageBody) {
            props.sendMessage(values.newMessageBody)
            values.newMessageBody = ''
        }
    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItem}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                <div>{messagesElements}</div>
                <AddMessageFormRedux onSubmit={addNewMessage}/>
            </div>
        </div>
    )
}



type NewMessageFormValuesTypeKeys = Extract<keyof NewMessageFormType, string>
type PropsType = {}

const maxLength200 = maxLengthCreator(200)
const AddMessageForm: FC<InjectedFormProps<NewMessageFormType, PropsType> & PropsType> = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                {createField<NewMessageFormValuesTypeKeys>('Enter your message', 'newMessageBody', [required, maxLength200], Textarea, s.textarea)}
                <button className={s.button}>Send</button>
            </div>
        </form>
    )
}


const AddMessageFormRedux = reduxForm<NewMessageFormType>({form: "dialogAddMessageForm"})(AddMessageForm)

export default Dialogs;